import AccountComponent from "../components/AccountComponent";
import tel_blue from "../static/image/picture/tel_blue.PNG";
import tel_pink from "../static/image/picture/tel_pink.PNG";
import heart from "../static/image/picture/heart.PNG";

export default function ContactContainer() {
    return (
        <div>
            <img src={heart} style={{width: '9%'}}/>
            <div>
                   <br/>
                    .
            </div>
            <div className="contactSection">
                <div style={{paddingLeft: '20%'}}>
                    <a href='tel:010-8875-8665'><img src={tel_blue} style={{width: '25%'}}/></a>
                    <div className="contactDiv">
                        <span className="notoSansRegular"> </span><br/>
                        
                    </div>
                </div>
                <div style={{paddingRight: '20%'}}>
                    <a href='tel:010-8875-8665'><img src={tel_pink} style={{width: '25%'}}/></a>
                    <div className="contactDiv">
                        <span className="notoSansRegular"> </span><br/>
                        
                    </div>
                </div>
            </div>
            <AccountComponent/>
        </div>
    )
        ;
}